.store-header{
    padding: .5rem 0rem;
    width: 100%;
    position: fixed;
    bottom: 0px;
    left: 0rem;
    display: flex;
    justify-content: space-evenly;
    background-color: yellowgreen;
}
.store-subcategory-opt{
    /* flex: 1; */
    display: flex;
    align-items: center;
}
.category-icon{
    margin-right: 1rem;
    font-size: large;
}
.store-category-opt{
    width: 2px;
    background-color: black;   
}
.store-cart{
    /* flex: 1; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart-icon{
    margin-right: 1rem;
    /* color: blue; */
    font-size: large;
}





/* styles for store body */
@import url('https://fonts.googleapis.com/css2?family=Signika:wght@300;600&display=swap');

.store-product-category{
    font-size: 1.5rem;
    text-align: center;
    font-family: 'Signika', sans-serif;
    font-weight: 600;
    color: gray;
    /* margin-bottom: -1rem; */
    text-shadow: 1px 1px 1px black;
    margin: 1rem;
}
.store-product-box{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.store-product-box:last-child{
    margin-bottom: 4rem;
}
.category-seperator{
    background-color: blue;
    height: .1rem;
}
.store-product{
    flex: 0 0 clamp(320px, 90%, 480px);
    display: grid;
    grid-template-rows: 2.4fr 1fr;
    place-self: center;
    margin: calc(5%);
    box-shadow: -1px 1px 11px 0px #786767;
}
.product-feature-thumbnail{
    background-repeat: no-repeat;
    background-size: cover;
}
.product-feature-thumbnail:hover{
    box-shadow: inset -1px -6px 20px 0px black;
}
.product-data{
    position: relative;
    padding: 0 1rem;
}
.product-title{
    font-weight: 600;
    font-size: 1.1rem;
    margin-top: 1rem;
}
.product-meta-data{
    display: flex;
    justify-content: space-between;
}
.product-price{
    font-size: 1.3rem;
    font-weight: 600;
    color: yellowgreen;
}
.product-price span{
    font-size: 1.4rem;
}
.show-more-btn{
    color: gray;
    font-size: .9rem;
    position: absolute;
    left: 1rem;
    bottom: .3rem;
    cursor: pointer;
}
.product-btns{
    display: flex;
    justify-content: center;
}
.add-to-cart-btn{
    width: 2.5rem;
    display: grid;
    place-items: center;
    height: 2.5rem;
    cursor: pointer;
    margin-top: 1rem;
    border: solid yellowgreen 1px;
}
.add-to-cart-btn:hover{
    background-color: yellowgreen;
}
.add-to-cart-btn:hover .product-cart-icon{
    color: white;
}
.product-cart-icon{
    color: yellowgreen;
}

.place-order-btn{
    display: grid;
    place-items: center;
    height: 2.5rem;
    padding: 0 .5rem;
    cursor: pointer;
    margin-top: 1rem;
    margin-right: .5rem;
    border: solid blue 1px;
}
.place-order{
    display: flex;
    justify-content: center;
    align-items: center;
    color: blue;
}
.place-order-btn:hover{
    background-color: blue;
}
.place-order-btn:hover .place-order-icon{
    color: white;
}
.place-order-btn:hover .place-order{
    color: white;
}
.place-order-icon{
    color: blue;
}
.product-category-data{
    padding: 1rem;
    font-family: 'Signika', sans-serif;
    font-size: 1.2rem;
    color: #433838;
    line-height: 1.5rem;
    text-align: center;
    background-color: #c5ccd9;
}








.store-category-body{
    display: flex;
    justify-content: space-between;
    animation-name: showCategory;
    animation-duration: .5s;
    background-color: lightcoral;
    height: 90vh;
    overflow-y: scroll;
}
.category-list{
    width: 100%;
    padding-inline-start: 0;
    margin-block-start: 0;
}
.store-category-list{
    background-color: lightcoral;
    display: block;
}
@keyframes showCategory{
    0%{
        transform: translateX(-100vw);
    }
    100%{
        transform: translateX(0vw);
    }
}
.store-category-list p{
    padding: 1rem;
    text-align: center;
    font-weight: 600;
    margin: 0;
    font-variant-caps: all-petite-caps;
    font-size: 1.4rem;
}
ul ul{
    padding-inline-start: 1rem;
    padding-inline-end: 1rem;
}
.store-subcategory-list{
    padding: 1rem;
    background-color: #f1a7a7;
    transition: all .2s;
    cursor: pointer;
    font-size: 1.1rem;
}
.store-subcategory-list:hover{
    background-color: gray;
}
.store-subcategory-list:active{
    background-color: rgb(97, 75, 75);
}







.store-cart-body{
    background-color: lightcoral;
    padding: 1rem 1rem 5rem 1rem;
    animation: showcart;
    animation-duration: .5s;
    height: 85vh;
    overflow-y: scroll;
}
@keyframes showcart{
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(0%);
    }
}
.store-cart-heading-wrapper{
    display: grid;
    place-items: center;
}
.store-cart-heading{
    display: flex;
    align-items: flex-start;
    font-family: 'Signika', sans-serif;
    font-weight: 700;
    font-size: 1.3rem;
    padding: 1rem 0;
}
.store-cart-heading span{
    margin-right: .5rem;
}
.store-cart-body-icon{
    width: 2rem;
    height: 2rem;
}

table{
    border: solid black 1px;
    width: 90%;
    margin: calc(5%);
}
th{
    border-right: solid black 1px;
    border-bottom: solid black 1px;
    padding: .5rem;
    background-color: #786767;
}
th:last-child{
    border-right: none;
}
td{
    text-align: center;
    padding: .5rem;
    border-right: solid black 1px;
}
td:last-child{
    border-right: none;
}
.cart-final-details{
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    margin: 1rem 2rem;
    border: solid black 1px;
    background-color: #786767;
}
.cart-total{
    font-weight: 600;
}
.cart-total-money{
    background-color: lightcoral;
    padding: 0 1rem;
}






.order-form-wrapper{
    display: grid;
    place-items: center;
}
.order-form{
    padding: 1rem;
    background-color: lightcoral;
    display: grid;
    width: 360px;
    place-items: center;
    border: solid black 1px;
}
.order-form-message{
    border-bottom: solid #000000 2px;
    text-align: center;
    width: fit-content;
    margin: 1rem;
    font-size: 1.3rem;
}
.order-input{
    border: none;
    outline: none;
    width: 90%;
    padding: 1rem .5rem 0 .5rem;
    font-size: 1.1rem;
    border-bottom: solid #000000 2px;
    background-color: lightcoral;
}
.order-btn-wrapper{
    display: grid;
    place-items: center;
    padding: 2rem 0 1rem 0; 
}
@media screen and (max-width:480px){
    .order-form{
        width: 90%;
    }
}
